<template>
    <div class="page-wrapper" v-show="isLoading">

        <div class="cart-tip" v-if="cartTip">{{cartTip}}</div>
        <div class="cart-body">
            <img class="cart-title" :src="imgGiveCartTitle">
            <div class="cart-content">
                <img class="img" v-lazy="activityInfo.img">
                <div class="cart-days" v-show="activityInfo.days">
                    <span class="day-count">{{activityInfo.days}}</span><span>天</span>
                </div>
            </div>
            <div v-if="isShowOpenApp" class="cart-button" @click="submitReceive">立即免费领取</div>
            <div v-else class="cart-button" @click="openApp">{{`打开APP${activityInfo.get_user && user_info.id === activityInfo.get_user?'使用':''}`}}</div>

        </div>
        <div class="cart-rule">
            <div class="divider">
                <div class="divider-title">领取规则</div>
            </div>
            <table class="rule-body">
                <tbody>
                    <tr :key="index" v-for="(rule,index) in rules ">
                        <td>{{index+1}}.</td>
                        <td>{{rule}}</td>
                    </tr>
                </tbody>
            </table>

        </div>

        <pop-box :show="isShowReceiveInfo" theme="1" @clickClosePop="closeModule">
            <div class="receive-info">
                <div class="title">恭喜你，领取成功</div>
                <div class="tip">打开APP，在【我的】页面，点击【查看详情】查看。</div>
                <div class="img-explain">
                    <img :src="receiveCartTipPath">
                </div>
                <div class="to-vip" @click="openApp">立享特权</div>
            </div>
        </pop-box>

    </div>
</template>

<script>
//亲友卡赠送

import API from '@/server/api'
import PopBox from '@/components/PopBox'
import { APP_DOWN_FROM } from '@/const/urls'
import { IMG_RECEIVE_CART_TIP, IMG_GIVE_CART_TITLE } from '@/const/images'
import share from '@/mixins/share'

const statuTyps = {
    obtain: 1, //已获取
    give: 2, //已赠送
    gain: 3, //已领取
    use: 4, //已使用
    overdue: 5, //已过期
}

let defaultActionPoint = {
    cid: "get_friends_vip_card",
    name: ""
}

export default {
    components: { PopBox },
    mixins: [share],
    name: "vip_give_friends",
    data() {
        return {
            isLoading: false,
            receiveCartTipPath: IMG_RECEIVE_CART_TIP,
            imgGiveCartTitle: IMG_GIVE_CART_TITLE,
            userInfo: {},
            isShowReceiveInfo: false,
            isShowOpenApp: false,
            cartTip: '',
            activityInfo: {},
            card_id: '',
            rules: [
                '此亲友卡为叮咚买菜绿卡会员体验卡，领取后即可获得相应天数的绿卡体验期，立享叮咚绿卡特权；',
                '体验卡特权有：可领每月不低于292元的绿卡专享券，购物可获2倍抵现积分，还有每周大量专享特价商品，买菜更划算；',
                '每张亲友卡仅限1人领取，每人每年仅限领取1张；',
                '亲友卡仅限非绿卡会员领取，如已开通绿卡并在有效期内将无法领取；',
                '领取成功后请至叮咚买菜APP内享用特权；',
                '如未领取，此亲友卡可分享链接后转赠他人。',
            ]
        };
    },
    watch: {
        user_login: {
            handler(val) {
                if (val === 0) {
                    this.loadDataInfo();
                }
            },
            immediate: true
        },
        user_info: {
            handler(val) {
                if (val.id) {
                    this.loadDataInfo();
                }
            },
            immediate: true
        },

    },
    methods: {
        async loadDataInfo() {
            const that = this;
            that.card_id = that.$route.query.card_id;
            defaultActionPoint.name = that.card_id;
            const result = await that.$http.get(API.vipApi('friendsCardDetail'), { card_id: that.card_id });

            if (result.data) {
                const { share_info } = result.data;
                that.isShowOpenApp = false;
                that.activityInfo = result.data;
                if (that.activityInfo.status == statuTyps.overdue) {
                    that.cartTip = '抱歉，领取失败，亲友卡已过期';
                } else if (that.activityInfo.get_user && that.user_info.id === that.activityInfo.get_user) {
                    that.cartTip = '你已领取该亲友卡';
                } else if (that.activityInfo.is_get) {
                    that.cartTip = '抱歉，领取失败，每年仅限领取1次，您已经领过啦 ';
                } else if (that.activityInfo.get_user && that.user_info.id !== that.activityInfo.get_user) {
                    that.cartTip = '抱歉，领取失败，体验卡已被他人领取';
                } else if (that.activityInfo.is_vip) {
                    that.cartTip = '抱歉，领取失败，你已经是绿卡会员啦'
                } else {
                    that.isShowOpenApp = true;
                }

                //设置分享信息
                if (share_info) {
                    that.updateShare(share_info);
                }
                that.log(Object.assign({
                    aid: 'index',
                    index: that.isShowOpenApp ? 1 : 0,
                    value: !that.isShowOpenApp ? that.cartTip : '',
                }, defaultActionPoint));

            } else {

                that.log(Object.assign({
                    aid: 'index',
                    index: 0,
                    value: result.error ? result.error.msg : '加载亲友卡异常',
                }, defaultActionPoint));

            }
            that.$nextTick(() => {
                that.isLoading = true;
            })

        },
        async submitReceive() {
            const that = this;
            if (!that.user_login) {
                that.$store.state.login.isShowLogin = true;
                return false
            }
            that.log(Object.assign({
                aid: 'get',
            }, defaultActionPoint));
            const { data } = await that.$http.post(API.vipApi('friendsCardGet'), { card_id: that.card_id });
            if (data) {
                that.isShowReceiveInfo = true;
                that.log(Object.assign({
                    aid: 'get_success',
                }, defaultActionPoint));

            }
        },
        openApp() {
            this.log(Object.assign({
                aid: 'go_and_use',
            }, defaultActionPoint));
            this.openUrl({ path: APP_DOWN_FROM, query: { s: 'qinyouka' } });
        },
        closeModule() {
            this.loadDataInfo();
            this.isShowReceiveInfo = false;
        },
    },

}
</script>