<template>
    <div>
        <transition name="box">
            <div class="mask-pop" v-show="popShow" @click="_closePop"></div>
        </transition>
        <transition name="box-content">
            <div class="pop-box" @click.stop v-show="popShow" :class="'theme-'+theme">
                <icon-font name="guanbiyoushangjiao" color="#cccccc" size="30" class="close-icon" @click="_closePop" v-if="theme==='0'"/>
                <icon-font name="gouwuchedankuangguanbi" color="#ffffff" size="50" class="close-icon" :class="'theme-'+theme" @click="_closePop" v-if="theme==='1'"/>
                <slot></slot>
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        name: 'pop-box',
        props: {
            show: {
                default: false,
                type: Boolean
            },
            //样式类型 0, 1
            theme: {
                default: '0',
                type: String
            }
        },
        data(){
            return {
                popShow: this.show
            }
        },
        watch:{
            show(){
                this.popShow = this.show;
            }
        },
        methods: {
            _closePop(){
                this.$emit('clickClosePop');
                this.popShow = false;
            }
        }
    };
</script>

<style lang="scss" scoped>
    .mask-pop {
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: 8;
        background: rgba(0, 0, 0, .6);
    }
    .pop-box {
        position: fixed;
        left: 0;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        transform-origin:50% 0;
        background: #FFFFFF;
        z-index: 9;
        margin: auto;
        width: 630px;
        box-sizing: border-box;
        padding: 65px 40px 40px 40px;
        border-radius: 12px;
        .close-icon {
            position: absolute;
            top: 30px;
            right: 30px;
        }
        &.theme-1{
            padding: 40px;
        }
        .close-icon.theme-1{
            top: auto;
            bottom: -110px;
            left: 50%;
            margin-left: -25px;
        }
    }

    .box-enter-active, .box-leave-active {
        transition: opacity .3s;
    }
    .box-enter, .box-leave-to{
        opacity: 0;
    }

    .box-content-enter-active {
        animation: bounce-in .3s;
    }
    .box-content-leave-active {
        animation: bounce-in .2s reverse;
    }

    @keyframes bounce-in {
        0% {
            transform: scale(0) translateY(-50%);
        }
        100% {
            transform: scale(1) translateY(-50%);
        }
    }

</style>

